import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {
  ListPurchasePackageDto,
  PlanOptionTypes,
  ResponseListPurchasePackageDto,
  SubscriptionService
} from "@youtap-web-lib";
import { GaService } from '../../../service/ga.service';

@Component({
  selector: 'subs-purchased-section',
  templateUrl: './subs-purchased-section.component.html'
})
export class SubsPurchasedSectionComponent implements OnInit {
  listLoading = false;
  listPurchasePackage!: ResponseListPurchasePackageDto;

  constructor(
    private subscriptionService: SubscriptionService,
    private router: Router,
    private gaService: GaService
  ) {}

  ngOnInit() {
    this.getPackagePurchasedList();
  }

  getPackagePurchasedList() {
    this.listLoading = true;
    const data: ListPurchasePackageDto = {
      listPurchasePackageInputPaid: {
        planOptionType: PlanOptionTypes.PAID,
        isExpired: false
      },
      listPurchasePackageInputPaidExpired: {
        planOptionType: PlanOptionTypes.PAID,
        isExpired: true
      }
    };

    this.subscriptionService.listPurchasePackage(data).subscribe({
      next: ({ data }) => {
        this.listLoading = false;
        this.listPurchasePackage = data
      },
      error: () => {
        this.listLoading = false;
      }
    });
  }

  goToHistorySubscription() {
    this.router.navigate(['/subscription/history']);
  }

  goToSubsPlanPage() {
    this.gaService.addEvent('list-subscription', 'profile', '');
    this.router.navigate(['/subscription/list-subscription']);
  }
}
