import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ListVoucher, VoucherService } from '@youtap-web-lib';

@Component({
  selector: 'my-voucher',
  templateUrl: './my-voucher.component.html'
})
export class MyVoucherComponent implements OnInit {
  listVoucher!: ListVoucher;
  loading = true;

  constructor(private router: Router, private voucherService: VoucherService) {}

  ngOnInit(): void {
    this.getListVoucher();
  }

  getListVoucher() {
    this.voucherService.listVoucher().subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.listVoucher = data.listVoucher;
      },
      error: (err) => {
        this.loading = false;
        return err;
      }
    });
  }

  goToHome() {
    this.router.navigate(['/'], {});
  }
}
