import { Component, OnInit } from '@angular/core';
import {
  StatusKyc,
  MembershipService,
  ProfileService,
  ResponseGetProfileDto,
  ResponseListPurchasePackageDto,
  RegisterService,
  CurrentTier,
  ScmAjakCuanService, AgentStatus,
} from '@youtap-web-lib';
import { Router } from '@angular/router';
import { AppData } from '../../../shared/data/app-data';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'dashboard-profile',
  templateUrl: './dashboard-profile.component.html'
})
export class DashboardProfileComponent implements OnInit {
  loading = false;
  loadingAgent = false;
  loadingKyc = false
  dataProfile!: ResponseGetProfileDto;
  listPurchasePackage!: ResponseListPurchasePackageDto;
  btnLoadingLogout = false;
  showConfirmLogoutModal = false;
  currentTier!: CurrentTier;
  kycStatus!: StatusKyc;
  isAgent!: AgentStatus;

  constructor(
    private profileService: ProfileService,
    private router: Router,
    public appData: AppData,
    private windowActionService: WindowActionService,
    private membershipService: MembershipService,
    private registerService: RegisterService,
    private gaService: GaService,
    private scmAjakcuanService: ScmAjakCuanService,
  ) { }

  ngOnInit() {
    this.getProfileData();
    this.getCurrentTier();
    this.getDetailAgent();
  }

  getStatusKyc() {
    this.loadingKyc = true;
    this.registerService.getStatusKyc().subscribe({
      next: ({ data }) => {
        this.loadingKyc = false;
        this.kycStatus = data.getStatusKyc.merchantRegistration?.merchantKyc.status;
      }
    });
  }

  getProfileData() {
    this.loading = true;
    this.profileService.getProfile().subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.dataProfile = data;
        this.getStatusKyc();
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  goToEditProfile() {
    this.gaService.addEvent('Edit_profile', 'profile', '');
    const queryParams = {
      merchantName: this.dataProfile.detailUser.merchant.name,
      name: this.dataProfile.detailUser.name,
      phone: this.dataProfile.detailUser.phone,
      email: this.dataProfile.detailUser.email,
      image: this.dataProfile.detailUser.image
    };
    this.router.navigate(['/profile/edit-profile'], {
      queryParams: queryParams,
      queryParamsHandling: 'merge'
    });
  }

  logout() {
    this.gaService.addEvent('Profile_logout', 'profile', '');
    this.btnLoadingLogout = true;
    this.profileService.logout().subscribe({
      next: () => {
        localStorage.clear();
        this.router.navigate(['/'], { replaceUrl: true }).then(() => {
          this.windowActionService.reload();
        });
        this.btnLoadingLogout = false;
      },
      error: () => {
        this.btnLoadingLogout = false;
      }
    });
  }

  getCurrentTier() {
    this.membershipService.currentTier().subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.currentTier = data.currentTier;
      },
      error: (err) => {
        this.loading = false;
        return err;
      }
    });
  }

  goToRegister() {
    // const stateData = {
    //   title: 'Verifikasi Akun Kamu Sekarang!',
    //   isFromRegister: false
    // };
    // const navigationExtras: NavigationExtras = {
    //   state: stateData
    // };
    // this.router.navigate(['/register/success-register'], navigationExtras);
    // if (!this.kycStatus) {
    //   this.router.navigate(['/register/login-kyc']);
    // }
    this.gaService.addEvent('Verifikasi_profile', 'profile', '');
    switch (this.kycStatus) {
      case StatusKyc.PENDING:
        this.router.navigate(['/register/login-kyc']);
        break;
      case StatusKyc.REJECTED:
        this.router.navigate(['/register/login-kyc']);
        break
      default:
        this.router.navigate(['/register/login-kyc']);
        break;
    }
  }

  goToMyVoucher() {
    this.gaService.addEvent('my-voucher', 'profile', '');
    this.router.navigate(['/profile/my-voucher'], {});
  }

  openEmail(url: string) {
    this.windowActionService.href(url);
  }

  openWhatsapp(url: string, type: string) {
    if (type === 'youtap') {
      this.gaService.addEvent('Youtap_contact', 'profile', '');
    }
    else if (type === 'djpktn') {
      this.gaService.addEvent('DJPKTN_contact', 'profile', '');
    }
    this.windowActionService.open(url, '_blank');
  }
  goToMembershipDetail() {
    this.gaService.addEvent('membership_detail_section_profile', 'profile', '');
    this.router.navigate(['/membership']);
  }

  getDetailAgent() {
    this.loadingAgent = true;
    this.scmAjakcuanService.detailAgent().subscribe({
      next: (data) => {
        this.loadingAgent = false;
        this.isAgent = data.data.detailAgent.status;
      }, error: () => {
        this.loadingAgent = false;
      }
    })
  }

  goToAjakCuan() {
    if (this.isAgent === 'AGENT_STATUS_ACTIVE') {
      this.gaService.addEvent('Ajakcuan_in_profile', 'ajakcuan', '');
      this.router.navigate(['/ajakcuan/dashboard']);
    } else {
      this.gaService.addEvent('Ajakcuan_in_profile', 'ajakcuan', '');
      this.router.navigate(['/ajakcuan/onboard']);
    }
  }

}
