import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { SuccessEditModalProfileComponent } from '../success-edit-modal-profile/success-edit-modal-profile.component';
import { Location } from '@angular/common';
import { environment } from '../../../../environments/environment';
import { AuthData } from '../../../shared/data/auth.data';
import { UploadFileService } from '../../../shared/service/upload-file/upload-file.service';
import { UploadSingleMediaGCSDto } from '../../../shared/service/upload-file/upload-file.dto';
import {
  MediaFolderTypes,
  ProfileService,
  UpdateUserDto,
  ValidationService
} from '@youtap-web-lib';

@Component({
  selector: 'edit-profile',
  templateUrl: './edit-profile.component.html'
})
export class EditProfileComponent implements OnInit {
  loading = true;
  profileForm!: FormGroup;
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @ViewChild('fileInput') fileInput: any;
  errorMessage = '';
  errorMessageFileType = '';
  error = false;
  constructor(
    private route: ActivatedRoute,
    private formBuilder: FormBuilder,
    private profileService: ProfileService,
    private uploadFileService: UploadFileService,
    private router: Router,
    private modalService: ModalService,
    private location: Location,
    private authData: AuthData
  ) {
    this.profileForm = this.formBuilder.group({
      merchantName: ['', Validators.compose([Validators.required])],
      name: ['', Validators.compose([Validators.required])],
      phone: ['', Validators.compose([Validators.required])],
      email: [
        '',
        Validators.compose([
          Validators.required,
          ValidationService.emailCompleteValidator
        ])
      ],
      image: ['']
    });
  }

  ngOnInit(): void {
    this.getProfileData();
  }

  // getProfileData() {
  //
  // }

  getProfileData() {
    this.loading = true;
    this.profileService.getProfile().subscribe({
      // eslint-disable-next-line  @typescript-eslint/no-explicit-any
      next: ({ data }: any) => {
        this.loading = false;
        this.profileForm.controls['merchantName'].setValue(
          data.detailUser.merchant.name
        );
        this.profileForm.controls['name'].setValue(data.detailUser.name);
        this.profileForm.controls['phone'].setValue(data.detailUser.phone);
        this.profileForm.controls['email'].setValue(data.detailUser.email);
        this.profileForm.controls['image'].setValue(
          data.detailUser.userDetail.profilePicture
        );
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  onButtonClickInputFile() {
    this.fileInput.nativeElement.click();
  }

  handleClickUpdateUser() {
    this.loading = true;
    const modalRef = this.modalService.open(SuccessEditModalProfileComponent, {
      title: '',
      closeButton: false
    });
    const data: UpdateUserDto = {
      updateUserInput: {
        // merchantName: this.profileForm.controls['merchantName'].value,
        // name: this.profileForm.controls['name'].value,
        email: this.profileForm.controls['email'].value,
        profilePicture: this.profileForm.controls['image'].value
      }
    };

    this.profileService.updateUser(data).subscribe({
      next: () => {
        this.loading = false;
        // this.authData.user.name = data.updateUserInput.name;
        // this.authData.user.merchant.name = data.updateUserInput.merchantName;
        this.authData.user.email = data.updateUserInput.email ?? '';
        this.authData.user.userDetail.profilePicture =
          data.updateUserInput.profilePicture ?? '';
        modalRef.close('');
        this.location.back();
      },
      error: () => {
        modalRef.close('');
        this.loading = false;
      }
    });
  }

  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  onFileChange(event: any) {
    this.errorMessage = '';
    this.errorMessageFileType = '';
    this.error = false;
    const selectedFile = event.target.files[0];
    const maxFileSizeInBytes = 10_000_000; //in kb  = 10mb
    const allowedExtensions = ['jpg', 'jpeg', 'png'];

    if (!selectedFile) {
      return;
    }

    if (selectedFile && selectedFile.size > maxFileSizeInBytes) {
      this.errorMessage = 'Error: Ukuran file maksimal 10 Mb';
      this.error = true;
    }

    const fileExtension = selectedFile.name.split('.').pop().toLowerCase();
    if (selectedFile && !allowedExtensions.includes(fileExtension)) {
      this.error = true;

      // File type is not supported
      this.errorMessageFileType = 'Error: Tipe file harus JPG, JPEG atau PNG';
      event.target.value = null; // Clear the file input field
    }

    if (!this.error) {
      const file: UploadSingleMediaGCSDto = {
        createSingleMediaInput: {
          file: event.target.files[0],
          folder: MediaFolderTypes.PROFILE_IMAGE
        }
      };

      this.uploadFileService
        .uploadSingleMediaGCS(environment.BOS_API_GRAPHQL_URL, file)
        .then((value) => {
          this.profileForm.controls['image'].setValue(
            value.uploadSingleMediaGCS.url
          );
        })
        .catch((err) => {
          console.error('error mantap', err);
        });
    }
  }
}
