import { Component } from '@angular/core';
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";

@Component({
  selector: 'success-edit-modal-profile',
  templateUrl: './success-edit-modal-profile.component.html'
})
export class SuccessEditModalProfileComponent extends Modal{
  showButton = false;

  onInjectInputs(inputs: InputModalDto): void {
    this.showButton = inputs.closeButton;
  }

  save(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }

}
