import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BasicContainerComponent } from '../../shared/layout/container/basic-container/basic-container.component';
import { DashboardProfileComponent } from './dashboard-profile/dashboard-profile.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { MyVoucherComponent } from './my-voucher/my-voucher.component';

const routes: Routes = [
  {
    path: 'dashboard',
    component: BasicContainerComponent,
    data: { canGoBack: true },
    children: [
      {
        path: '',
        component: DashboardProfileComponent,
        title: 'Profil-Kamu',
        data: {
          pageTitle: "Profile",
        }
      }
    ]
  },
  {
    path: 'edit-profile',
    component: BasicContainerComponent,
    data: { canGoBack: true },
    children: [
      {
        path: '',
        component: EditProfileComponent,
        title: 'Edit-Profil',
        data: {
          pageTitle: "Edit Profile",
        }
      }
    ]
  },
  {
    path: 'my-voucher',
    component: BasicContainerComponent,
    data: { canGoBack: true },
    children: [
      {
        path: '',
        component: MyVoucherComponent,
        title: 'Voucher Saya',
        data: {
          pageTitle: "Voucher Saya",
        }
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfileRoutingModule {}
