import { Injectable } from '@angular/core';
import {
  ResponseUploadSingleMediaGCSDto,
  ResponseUploadSingleMediaRegistrationGCSDto,
  UploadSingleMediaGCSDto
} from './upload-file.dto';
import { HttpClient } from '@angular/common/http';
import {environment} from "../../../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class UploadFileService {
  constructor(private httpClient: HttpClient) {}

  uploadSingleMediaGCS(
    url: string,
    data: UploadSingleMediaGCSDto
  ): Promise<ResponseUploadSingleMediaGCSDto> {
    const operations = {
      operationName: 'uploadSingleMediaGCS',
      query: `mutation uploadSingleMediaGCS($createSingleMediaInput: CreateSingleMediaInput!) {
      uploadSingleMediaGCS(createSingleMediaInput: $createSingleMediaInput) {
      url
    }}`,
      variables: data
    };
    const _map = {
      file: ['variables.createSingleMediaInput.file']
    };
    const fd = new FormData();
    fd.append('operations', JSON.stringify(operations));
    fd.append('map', JSON.stringify(_map));
    fd.append('file', data.createSingleMediaInput.file, data.createSingleMediaInput.file.name);
    /* eslint-disable @typescript-eslint/no-explicit-any */
    return new Promise<ResponseUploadSingleMediaGCSDto>((resolve, reject) => {
      this.httpClient
        .post<ResponseUploadSingleMediaGCSDto>(
          environment.BOS_API_GRAPHQL_URL,
          fd
        )
        .toPromise()
        .then(({ data }: any) => {
          if (data) {
            resolve(data);
          } else {
            reject(data);
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  }
  uploadSingleMediaRegistrationGCS(
    url: string,
    data: UploadSingleMediaGCSDto
  ): Promise<ResponseUploadSingleMediaRegistrationGCSDto> {
    const operations = {
      operationName: 'uploadSingleMediaRegistrationGCS',
      query: `mutation uploadSingleMediaRegistrationGCS($createSingleMediaInput: CreateSingleMediaInput!) {
      uploadSingleMediaRegistrationGCS(createSingleMediaInput: $createSingleMediaInput) {
      url
    }}`,
      variables: data
    };
    const _map = {
      file: ['variables.createSingleMediaInput.file']
    };
    const fd = new FormData();
    fd.append('operations', JSON.stringify(operations));
    fd.append('map', JSON.stringify(_map));
    fd.append('file', data.createSingleMediaInput.file, 'test-upload');
    return new Promise<ResponseUploadSingleMediaRegistrationGCSDto>((resolve, reject) => {
      this.httpClient
        .post<ResponseUploadSingleMediaRegistrationGCSDto>(
          environment.BOS_API_GRAPHQL_URL,
          fd
        )
        .toPromise()
        .then((val) => {
          if (val) {
            resolve(val);
          } else {
            reject(val);
          }
        })
        .catch((err) => {
          reject(err);
        });
    });
  }
}
